import type { DropZoneProps } from "@shopify/polaris";
import { Box, DropZone, InlineError, InlineStack, Thumbnail } from "@shopify/polaris";
import { NoteIcon } from "@shopify/polaris-icons";
import { filesize } from "filesize";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import type { Control } from "react-hook-form";
import { useController } from "react-hook-form";
import { useApi } from "../../GadgetProvider.js";
import type { GadgetOnlyImageFileFieldValidation, GadgetRangeFieldValidation } from "../../internal/gql/graphql.js";
import { isAutoFileFieldValue, type AutoFileFieldValue } from "../../validationSchema.js";
import { useFieldMetadata } from "../hooks/useFieldMetadata.js";

const imageFileTypes = ["image/jpeg", "image/png", "image/svg+xml", "image/webp", "image/gif"];

export const PolarisAutoFileInput = (props: { field: string; control?: Control<any> } & DropZoneProps) => {
  const [openFileDialog, setOpenFileDialog] = useState(false);
  const { field: fieldApiIdentifier, control, ...rest } = props;
  // For showing a preview of the file
  const [imageThumbnailURL, setImageThumbnailURL] = useState<string>();
  const { path, metadata } = useFieldMetadata(fieldApiIdentifier);
  const api = useApi();

  const {
    field: fieldProps,
    fieldState: { error },
  } = useController({
    control,
    name: path,
  });

  const rangeValidation = useMemo(
    () => metadata.configuration.validations.find((validation) => validation?.__typename === "GadgetRangeFieldValidation"),
    [metadata]
  ) as GadgetRangeFieldValidation | undefined;

  useEffect(() => {
    if (fieldProps.value && isAutoFileFieldValue(fieldProps.value) && fieldProps.value.url) {
      setImageThumbnailURL(fieldProps.value.url);
    }
  }, [fieldProps.value]);

  const onlyImages = useMemo(() => {
    const imageValidation = metadata.configuration.validations.find(
      (validation) => validation?.__typename === "GadgetOnlyImageFileFieldValidation"
    ) as GadgetOnlyImageFileFieldValidation | undefined;
    if (!imageValidation) return;

    const acceptedTypes = [...imageFileTypes];
    if (imageValidation.allowAnimatedImages) {
      acceptedTypes.push("image/gif");
    }

    return { acceptedTypes, allowAnimatedImages: imageValidation.allowAnimatedImages };
  }, [metadata]);

  const handleDropZoneDrop = useCallback(
    async (_dropFiles: File[], acceptedFiles: File[], _rejectedFiles: File[]) => {
      const file = acceptedFiles[0];
      const baseFileValue = {
        fileName: file.name,
        mimeType: file.type,
      } satisfies AutoFileFieldValue;

      setImageThumbnailURL(window.URL.createObjectURL(file));

      if (
        rangeValidation &&
        ((rangeValidation.min && file.size < rangeValidation.min) || (rangeValidation.max && file.size > rangeValidation.max))
      ) {
        fieldProps.onChange({
          ...baseFileValue,
          $invalidFileSize: file.size,
        } satisfies AutoFileFieldValue);
        return;
      }

      fieldProps.onChange({
        ...baseFileValue,
        $uploading: true,
      } satisfies AutoFileFieldValue);

      // Upload the file in the background
      const { url, token } = await (api as any).getDirectUploadToken();
      await fetch(url, {
        method: "PUT",
        headers: {
          "Content-Type": file.type,
        },
        body: file,
      });

      fieldProps.onChange({
        directUploadToken: token,
        ...baseFileValue,
      } satisfies AutoFileFieldValue);
    },
    [api, fieldProps, rangeValidation]
  );

  const fileUpload = useMemo(() => {
    if (fieldProps.value) return null;

    const actionHintParts = ["Accepts"];
    if (onlyImages) {
      actionHintParts.push(`.jpg, .webp, .svg${onlyImages.allowAnimatedImages ? ", .gif" : ""}, and .png`);
    }

    if (rangeValidation) {
      if (rangeValidation.min) {
        if (rangeValidation.max) {
          actionHintParts.push(`between ${filesize(rangeValidation.min)} and ${filesize(rangeValidation.max)}`);
        } else {
          actionHintParts.push(`larger than than ${filesize(rangeValidation.min)}`);
        }
      } else {
        if (rangeValidation.max) {
          actionHintParts.push(`smaller than ${filesize(rangeValidation.max)}`);
        }
      }
    }

    return <DropZone.FileUpload actionTitle="Add file" actionHint={actionHintParts.length === 1 ? "" : actionHintParts.join(" ")} />;
  }, [fieldProps.value, onlyImages, rangeValidation]);

  const uploadedFiles = useMemo(() => {
    const value = fieldProps.value;
    if (!value || !isAutoFileFieldValue(value)) return null;
    return (
      <Box padding="100">
        <InlineStack gap="200" blockAlign="center">
          <Thumbnail
            size="small"
            alt={value.fileName}
            source={imageThumbnailURL && imageFileTypes.includes(value.mimeType) ? imageThumbnailURL : NoteIcon}
          />
          <div>
            {value.$uploading ? "(Uploading) " : ""}
            {value.fileName}
          </div>
        </InlineStack>
      </Box>
    );
  }, [fieldProps.value, imageThumbnailURL]);

  const dropZoneLabel = (
    <>
      {metadata.name} {metadata.requiredArgumentForInput ? <span style={{ color: "var(--p-color-text-critical)" }}>*</span> : null}
    </>
  );

  return (
    <>
      <DropZone
        variableHeight
        openFileDialog={openFileDialog}
        label={dropZoneLabel}
        allowMultiple={false}
        onDrop={(...args) => {
          void handleDropZoneDrop(...args);
        }}
        onFileDialogClose={() => setOpenFileDialog((prev) => !prev)}
        {...rest}
        accept={onlyImages?.acceptedTypes?.join(",")}
      >
        {uploadedFiles}
        {fileUpload}
      </DropZone>
      {error && error.message && (
        <div style={{ marginTop: "4px" }}>
          <InlineError fieldID={fieldApiIdentifier} message={error.message} />
        </div>
      )}
    </>
  );
};
